/**
 * Global breakpoints for your application.
 * @note Always set the `em` context to 16 when you’re using them for media
 * queries. Otherwise, if you change `$font-size` you’ll get wonky values that
 * technically work but aren’t what you wanted.
 * @see https://zellwk.com/blog/media-query-units/
 * @type {Map}
 */
*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  -webkit-tap-highlight-color: transparent;
  -webkit-backface-visibility: hidden; }

body {
  margin: 0; }

figure {
  margin: 0; }

ol,
ul {
  margin-top: 0;
  list-style: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

svg {
  display: inline-block;
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle; }

address,
cite {
  font-style: normal; }

.font {
  color: #fff;
  font-family: sans-serif;
  font-size: 11px;
  letter-spacing: 0.09em;
  line-height: 1.364;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase; }

.video-container {
  position: relative;
  width: 100%;
  height: 0;
  padding: 0 0 56.25%;
  overflow: hidden; }

.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  transition: transform 300ms ease; }

.video[id$='1'] {
  transform: translateX(-100%); }

.video[id$='2'] {
  transform: translateX(100%); }

.video.active {
  transform: translateX(0); }

.drag-me {
  display: flex;
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  transform: translateX(-50%) translateY(-50%) skew(-10deg);
  border: 3px solid #fff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  cursor: move;
  user-select: none; }

.track-me {
  transition: background 500ms linear;
  background: #4e494e; }

.click-me {
  padding: 0.5em 1em;
  transform: skew(-10deg);
  border: 3px solid #fff;
  background: #4e494e;
  line-height: 2; }
  .click-me:focus, .click-me:hover {
    background: #fff;
    color: #4e494e; }
  .click-me:active {
    opacity: 0.6; }

#swap-video {
  position: absolute;
  z-index: 1;
  top: 50px;
  left: 50px; }
